import React, { Component } from "react";

import "bootstrap/dist/css/bootstrap.min.css";
import "./Components.css";

export function SemesterPlan({
	nodesData, linksData, result, tsFunc
}) {
	
	// const { nodesData, linksData } = this.props;
	const containerRef = React.useRef("huurdur");
	
	React.useEffect(() => {
		
		const ids = nodesData.map((a) => a.id);
		const links = linksData.filter((d) => ids.includes(d.source) && ids.includes(d.target));
		
		if (containerRef.current) {
			let result = topSort(nodesData, links)
			
			tsFunc(result)
			console.log("result", result)
		}
		
		//   return destroyFn;
    }, [nodesData]);
	
	const valid = (sub, sem, i, l) => {
		let pred1 = l > i
		if (pred1) {
			return sub[i].semester.toLowerCase().includes(sem) ? result[i + 0].code + " " + result[i + 0].semester : "";
		}
		return "";
	}
	
	const valid2 = (sub, sem, i, l) => {
		let pred1 = l > i
		if (pred1) {
			return sub[i].semester.toLowerCase().includes(sem);
		}
		return false;
	}
	
	// let semester = [];
	let idk = []
	let l = result.length
	
	let i = 0;
	let j = 0;
	// let i1 = 0;
	// let i2 = 0;
	
	let sem1 = [];
	let sem2 = [];
	
	while (i < l) {
		
		if (valid2(result, "høst", i, l) ) {
			sem1.push(result[i]);
			i++;
		}
		else if (valid2(result, "vår", i, l)) {
			sem2.push(result[i]);
			i++;
        }
	}
	// console.log(sem1, sem2)
	
	while (sem1.length > 0 || sem2.length > 0) {
	  let s1 = sem1.length > 0 ? sem1.pop().code : "";
	  let s2 = sem1.length > 0 ? sem1.pop().code : "";
	  let s3 = sem1.length > 0 ? sem1.pop().code : "";
	  let s4 = sem2.length > 0 ? sem2.pop().code : "";
	  let s5 = sem2.length > 0 ? sem2.pop().code : "";
	  let s6 = sem2.length > 0 ? sem2.pop().code : "";
	
	  idk.push(
	    <>
	      <tr style={{ backgroundColor: "#a55" }}>
	        <td>{s1}</td>
	        <td>{s2}</td>
	        <td>{s3}</td>
	      </tr>
	      <tr style={{ backgroundColor: "#55a" }}>
	        <td>{s4}</td>
	        <td>{s5}</td>
	        <td>{s6}</td>
	      </tr>
	    </>
	  );
	};
	
	// console.log(sem1, sem2);
	
    return (
		<table className="myTable">
        <thead>
          <tr>
            <th>1</th>
            <th>2</th>
            <th>3</th>
          </tr>
        </thead>
        <tbody>
          {idk}
        </tbody>
      </table>
    );
	
}

export default SemesterPlan;

